<template>
  <div>
    hey
    <ul>
      <li v-for='event in events'>{{ event }}</li>
    </ul>
  </div>
</template>

<script>
import $ from 'jquery'

export default {
  data () {
    return {events: this.$store.state.events}
  },
  created () {
    this.getEvents()
  },
  methods: {
    getEvents () {
      $.ajax({
        method: 'GET',
        url: '/api/events',
        success: events => {
          this.$store.dispatch('getEvents', events)
        }
      })
    }
  },
  computed: {
    events () {
      return this.$store.getters.events
    }
  }
}
</script>

<style>
</style>



