import Vue from 'vue/dist/vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    events: []
  },
  mutations: {
    RECEIVE_EVENTS (state, events) {
      state.events = events
    }
  },
  actions: {
    getEvents ({ commit }, events) {
      commit('RECEIVE_EVENTS', events)
    }
  },
  getters: {
    events: state => state.events
  }
})

export default store



