import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import Profile from './components/Profile.vue'
import Events from './components/Events.vue'
Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  base: __dirname,
  routes: [
    {
      path: '/',
      components: {
        default: App
      }
    },
    {
      path: '/profile',
      components: {
        default: Profile
      }
    },
    {
      path: '/events',
      components: {
        default: Events
      }
    }
  ]
})

const app = new Vue({
  el: '#app',
  router,
  template: '<div><router-view name="events"></router-view><router-view></router-view></div>'
})

router.start(app, '#app')



